import { message, Spin } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import NoImg from '../../assets/noimg.png';
const BlogDetails = ({ match }) => {
  const [data, setdata] = useState();
  const [spin, setSpin] = useState();
  const {
    params: { blogId },
  } = match;
  useEffect(() => {
    setSpin(true);
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/blog/${blogId}`)
      .then((resp) => {
        setSpin(false);
        setdata(resp.data);
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        message.error('error');
      });
  }, [blogId]);
  const cover = {
    backgroundImage: `url(${data.coverpic !== null ? data.coverpic : NoImg})`,
  };
  return (
    <div className='container blog-details'>
      <Helmet>
        <title>Blogs - Ginnovation Club</title>
      </Helmet>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <img src={cover} alt='blog-cover' />
      <header>
        <div
          className='profile-img'
          style={{
            backgroundImage:
              'url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSzIiSQ9vLEJPym-UMILKt7v-XdHSQ4qLlIQA&usqp=CAU")',
          }}
        ></div>
        <h4>Ashutosh Bisoyi</h4>
        <p>Created on 12/02/21</p>
      </header>
      <hr />
      <div className='blog-content'>
        <header>
          <h1>{data.blogTitle}</h1>
          <h4>{data.subTitle}</h4>
        </header>
        <p>{data.blogContent}</p>
      </div>
    </div>
  );
};

export default BlogDetails;
